$header-height: 128px;

$cl-main : #CE328A;
$cl-white : #FFFFFF;
$cl-black : #191919;
$cl-gray : #545454;




@mixin font-heading {
    font-size: 32px;
    line-height: 42px;
    font-weight: bold;
    color: #685F5F;
    font-family: Helvetica-Bold, sans-serif;
}

@mixin font-sub-heading {
    font-size: 24px;
    line-height: 36px;
    font-weight: bold;
    color: #685F5F;
    font-family: Helvetica-Bold, sans-serif;
}

@mixin font-body {
    font-size: 12px;
    line-height: 18px;
    font-weight: normal;
    font-family: Helvetica-Regular, sans-serif;
}

@mixin mobile {
    @media (max-width: 768px) {
        @content;
    }
}