.s-fin {
    transition: 1s ease;
    .s-fin-list {
        position: absolute;
        top: calc(50% - 450px);
        left: calc(50% - 600px);
        z-index: 10;
        width: 1200px;
        height: 900px;

        .facility {
            position: absolute;
            width: 48px;
            height: 48px;

            .hit {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 10;
                width: 100%;
                height: 100%;
                cursor: pointer;

                &:hover ~ .pin {
                    background: #A9A4A4;
                    .pin-head {
                        color: #191919;
                    }
                }
            }

            .pin {
                position: absolute;
                top: 14px;
                left: 14px;
                width: 20px;
                height: 20px;
                border-radius: 14px;
                background: #191919;
                transition: 0.5s ease;
                
                .pin-head {
                    position: absolute;
                    top: 1px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                    transition: 0.3s ease;
                    font-size: 10px;
                    font-family: Helvetica-Bold;
                    line-height: 10px;
                    color: #F0EBE0
                }
            }

            .pin-headline {
                position: absolute;
                top: 23px;
                opacity: 1;
                .pin-title {
                    position: absolute;
                    bottom : 2px;
                    font-size: 10px;
                    line-height: 14px;
                }

                .pin-icon {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    bottom: -11.5px;
                    width: 24px;
                    height: 24px;
                    border: 1px solid #191919;
                    border-radius: 50%;
                    background: #F0EBE0;

                    img {
                        width: 18px;
                        height: 18px;
                    }
                }

                .pin-line {
                    width: 140px;
                    height: 1px;
                    background: #191919;
                }
            }

            .hide-pin-headline {
                opacity: 0;
            }


            //premium area
            &:nth-child(1) { top: calc(50px); left: calc(714px); 
                .pin-headline {left: 34px;
                    .pin-title {right: 4px;}
                    .pin-icon {right: -24px;}
                }
            }
            //meditation garden
            &:nth-child(2) { top: calc(246px); left: calc(610px);
                .pin-headline {right: 34px;
                    .pin-title {left: 4px;}
                    .pin-icon {left: -24px;}
                }
            }
            //play garden
            &:nth-child(3) { top: calc(480px); left: calc(506px); 
                .pin-headline { right: 34px;
                    .pin-title {left: 4px;}
                    .pin-icon {left: -24px;}
                }
            }
            // forest garden
            &:nth-child(4) { top: calc(360px); left: calc(610px); 
                .pin-headline { left: 34px;
                    .pin-title {right: 4px;}
                    .pin-icon {right: -24px;}
                }
            }
            // pet area
            &:nth-child(5) { top: calc(700px); left: calc(590px); 
                .pin-headline {left: 34px;
                    .pin-title {right: 4px;}
                    .pin-icon {right: -24px;}
                }
            }
            // organic garden
            &:nth-child(6) { top: calc(300PX); left: calc(740PX); 
                .pin-headline { left: 34px;
                    .pin-title {right: 4px; text-align: right;}
                    .pin-icon {right: -24px;}
                }
            }
            //main building
            &:nth-child(7) { top: calc(440px); left: calc(560PX); 
                .pin-headline {right: 34px;
                    .pin-title {left: 4px;}
                    .pin-icon {left: -24px;}
                }
            }
            // bbq deck
            &:nth-child(8) { top: calc(374px); left: calc(537PX); 
                .pin-headline {
                    right: 34px;
                    .pin-title {left: 4px;}
                    .pin-icon {left: -24px;}
                }
            }
            // Sauna & Restaurant
            &:nth-child(9) { top: calc(190px);left: calc(736px);
                .pin-headline {
                    left: 34px;
                        .pin-title {right: 4px;}
                        .pin-icon {right: -24px;}
                }
            }
            // Funicular
            &:nth-child(10) { top: calc(610px); left: calc(484px);
                .pin-headline {
                    right: 34px;
                    .pin-title {left: 4px;}
                    .pin-icon {left: -24px;}
                }
            }
            // Centre of forest
            &:nth-child(11) { top: calc(420px); left: calc(810px);
                .pin-headline {
                    left: 34px;
                    .pin-title {right: 4px;}
                    .pin-icon {right: -24px;}
                }
            }
            // sunset bridge
            &:nth-child(12) { top: calc(490px); left: calc(924px);
                .pin-headline {
                    left: 34px;
                    .pin-title {right: 4px;}
                    .pin-icon {right: -24px;}
                }
            }
        }

        .facility-selected {
            .pin {
                background: #A9A4A4;
                    .pin-head {
                        color: #191919;
                    }
            }
        }
    }

    .bg-sono-fin {
        position: absolute;
        top: calc(50% - 450px);
        left : calc(50% - 600px);
        width: 1200px;
        opacity: 0.8;
    }
}