.area-info {
    position: absolute;
    top: 0;
    left: 0;

    .info {
        position: fixed;
        max-width: 664px;
        width: 50%;
        height: calc(100% - 48px);
        padding: 42px 80px 42px 32px;
        top: 24px;
        right: -50%;
        z-index: -10;
        transition: 0.7s ease-in-out;
        opacity: 0;
        background: rgba(255, 255, 255);
        border-radius: 20px;
        overflow: scroll;
        box-shadow: 4px 12px 24px rgba(0, 0, 0, 0.12);
        cursor: auto;

        -ms-overflow-style: none;
         &::-webkit-scrollbar{
           display:none;
         }

        @include mobile {
            top: inherit;
            bottom: -70%;
            left: 21px;
            max-width : calc(100% - 42px);
            width: 100%;
            height: 70%;
        }

        .close {
            position: absolute;
            top: 42px;
            right: 26px;
            width: 28px;
            height: 28px;
            background: #eee;
            border-radius: 50%;
            cursor: pointer;
            transition: 0.3s ease;

            &:hover {
                .bar {
                    background: #A9A4A4;
                }
            }

            .bar {
                display: block;
                position: absolute;
                top: 14px;
                left: 5px;
                width: 17px;
                height: 1px;
                background: #685F5F;
                transition: 0.3s ease;

                &:first-child {
                    transform : rotate(45deg)
                }

                &:last-child {
                    transform: rotate(135deg);
                }
            }
        }

        .expander {
            position: absolute;
            top: 88px;
            right: 26px;
            width: 28px;
            height: 28px;
            cursor: pointer;
            border-radius: 50%;
            background: #eee;
            transition: 0.3s ease;

            &:hover {
                .bar {
                    background: #A9A4A4;
                    &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid #A9A4A4;}
                    &:nth-child(2) {top: 6px; left: 6px; width: 5px;}
                    &:nth-child(3) {top: 6px; left: 6px; height: 5px;}
                    &:nth-child(4) {top: 6px; right: 6px; width: 5px;}
                    &:nth-child(5) {top: 6px; right: 6px; height: 5px;}
                    &:nth-child(6) {bottom: 6px; left: 6px; width: 5px;}
                    &:nth-child(7) {bottom: 6px; left: 6px; height: 5px;}
                    &:nth-child(8) {bottom: 6px; right: 6px; width: 5px;}
                    &:nth-child(9) {bottom: 6px; right: 6px; height: 5px;}
                }
            }

            .bar {
                position: absolute;
                display: block;
                width: 1px;
                height: 1px;
                background: #685F5F;
                transition: 0.3s ease;
                border-radius: 1px;

                &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid #685F5F;}
                &:nth-child(2) {top: 7px; left: 7px; width: 5px;}
                &:nth-child(3) {top: 7px; left: 7px; height: 5px;}
                &:nth-child(4) {top: 7px; right: 7px; width: 5px;}
                &:nth-child(5) {top: 7px; right: 7px; height: 5px;}
                &:nth-child(6) {bottom: 7px; left: 7px; width: 5px;}
                &:nth-child(7) {bottom: 7px; left: 7px; height: 5px;}
                &:nth-child(8) {bottom: 7px; right: 7px; width: 5px;}
                &:nth-child(9) {bottom: 7px; right: 7px; height: 5px;}
            }
        }

        .reduction {
            .bar {
                &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid #685F5F;}
                &:nth-child(2) {top: 6px; left: 6px; width: 5px;}
                &:nth-child(3) {top: 6px; left: 6px; height: 5px;}
                &:nth-child(4) {top: 6px; right: 6px; width: 5px;}
                &:nth-child(5) {top: 6px; right: 6px; height: 5px;}
                &:nth-child(6) {bottom: 6px; left: 6px; width: 5px;}
                &:nth-child(7) {bottom: 6px; left: 6px; height: 5px;}
                &:nth-child(8) {bottom: 6px; right: 6px; width: 5px;}
                &:nth-child(9) {bottom: 6px; right: 6px; height: 5px;}
                
                &:hover {
                    .bar {
                        &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid #685F5F;}
                        &:nth-child(2) {top: 7px; left: 7px; width: 5px;}
                        &:nth-child(3) {top: 7px; left: 7px; height: 5px;}
                        &:nth-child(4) {top: 7px; right: 7px; width: 5px;}
                        &:nth-child(5) {top: 7px; right: 7px; height: 5px;}
                        &:nth-child(6) {bottom: 7px; left: 7px; width: 5px;}
                        &:nth-child(7) {bottom: 7px; left: 7px; height: 5px;}
                        &:nth-child(8) {bottom: 7px; right: 7px; width: 5px;}
                        &:nth-child(9) {bottom: 7px; right: 7px; height: 5px;}
                    }
                }
            }
        }

        .info-content {
            margin: 0 auto;
            max-width: 664px;
            .info-header-section {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .copy-heading {
                    padding-right: 24px;
                }

                .info-illust {
                    width: 148px;
                }
            }

            .info-summary {
                padding: 24px 0 28px;
            }

            .info-gallery {
                padding: 28px 0;
                .copy-sub-heading {
                    margin-bottom: 24px;
                }
                
                .info-image-list {
                    display: flex;
                    margin-bottom: 10px;

                    li {
                        margin-right: 8px;
                        color: #A9A4A4;
                        font-size: 14px;
                        cursor: pointer;

                        &:hover {
                            color: #685F5F;
                            text-decoration: underline;
                        }
                    }

                    .selected {
                        color: #685F5F;
                        text-decoration: underline;
                    }
                }

                .info-image-wrap {
                    position: relative;
                    width: 100%;
                    height: 360px;
                    overflow: hidden;
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        transition: 0.3s ease;
                        opacity: 0;
                    }

                    .image-front {
                        opacity: 1;
                    }

                    .image-back {
                        opacity: 0;
                    }
                }

            }

            .info-spec {
                padding: 28px 0;
                .copy-sub-heading {
                    margin-bottom: 24px;
                }

                .info-spec-wrap {
                    display: flex;

                    .info-spec-block {
                        margin-right: 12px;
                        padding: 14px 16px;
                        text-align: center;
                        border-radius: 6px;
                        background: rgba(169, 164, 164, 0.4);
                        p {
                            font-size: 14px;
                            line-height: 22px;
                            margin-bottom: 10px;
                        }
                        span {
                            font-size: 18px;
                            font-weight: bold;
                            line-height : 24px;
                        }
                    }
                }
            }

            .info-fac-service {
                padding: 28px 0;
                .copy-sub-heading {
                    margin-bottom: 24px;
                }

                .info-fac-service-wrap {
                    width: 100%;
                    .info-fac-service-service {
                        .info-fac-service-service-heading {
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            padding: 12px 0;

                            h4 {
                                font-size: 18px;
                                font-weight: bold;
                                span {
                                    font-weight: bold;
                                    color: #A9A4A4;
                                    margin-right: 24px;
                                }
                            }

                            .stretch-button {
                                position: relative;
                                width: 28px;
                                height: 28px;
                                cursor: pointer;
                                span {
                                    position: absolute;
                                    display: block;
                                    width: 2px;
                                    height: 2px;
                                    background: #333;
                                    transition: 0.3s ease;
                                    border-radius: 1px;

                                    &:first-child { top: 13px; left: 5px; width: 18px;}
                                    &:last-child { top: 5px; left: 13px; height: 18px;}
                                }
                            }

                            .toggle {
                                span {
                                    &:last-child {
                                        opacity: 0;
                                        transition: 0.5s ease;
                                        transform: rotate(90deg);
                                    }
                                }
                            }
                        }

                        .info-fac-service-content {
                            overflow: hidden;
                            padding: 0;
                            max-height: 0;
                            transition: 0.5s ease;
                            opacity: 0;

                            .info-service-copy {
                                padding: 18px 0;
                                .info-service-name {
                                    font-size: 14px;
                                    line-height: 22px;
                                    font-weight: bold;
                                    color: #685F5F;
                                    text-decoration: underline;
                                }
                                .info-service-desc {
                                    margin-top: 424px;
                                    color: #A9A4A4;
                                }
                            }
                        }

                        .stretched {
                            max-height: 1000px;
                            height: 100%;
                            transition: 0.5s ease;
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }

    .info-showing {
        right: 48px;
        opacity: 1;
        z-index: 900;
        transition: 0.7s ease-in-out;
        transition-delay: 0.3s;

        @include mobile {
            bottom: 0;
        }
    }

    .info-expanding {
        top: 24px;
        right: 48px;
        max-width: 100%;
        width: calc(100% - 96px);
        height: calc(100% - 48px);
        background: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(8px);

        @include mobile {
            top: inherit;
            bottom: 0;
            left: 0;
            max-width : 100%;
            width: 100%;
            height: 100%;
        }
    }
}